
















import Vue from 'vue'

export default Vue.extend({
  props: {
    title: {
      type: String
    },
    showLeft: {
      type: Boolean,
      default: true
    },
    showRight: {
      type: Boolean,
      default: true
    }
  },
  methods: {
    handleLeft() {
      this.$emit('left')
    },
    handleRight() {
      this.$emit('right')
    }
  }
})
