










import Vue from 'vue'
import Tag from '@/components/Tag.vue'
import Dates from '@/classes/Dates'

export default Vue.extend({
  name: 'TaskItem',
  components: {
    Tag
  },
  props: {
    task: {
      type: Object,
      required: true
    },
    showStatus: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    taskId(): string {
      return this.task.id
    },
    taskTitle(): string {
      return this.task.title || ''
    },
    taskStatus(): string {
      return this.task.status || 'unclassified'
    },
    taskDue(): string {
      return this.task.dueDate ? Dates.dateToPretty(this.task.dueDate) : 'No due date'
    },
    taskClass(): string {
      if (this.task.status.toLowerCase() === 'done' || this.task.status.toLowerCase() === 'archive') {
        return 'complete'
      } else {
        return ''
      }
    }
  },
  methods: {
    handleClick(): void {
      this.$router.push(`/tasks/${this.taskId}`)
    }
  }
})
